
.code-wrap {
  margin-top: 10px;
}
.code-wrap.code-wrap-api {
    padding: 30px;
    padding-top: 0;
}
.code-wrap.code-wrap-overview {
    background: #f9f9f9;
    padding: 10px 15px;
    border: 1px solid main-color;
    border-radius: 5px;
    margin-top: 0;
}
.code-wrap .code-title {
    font-size: 13px;
    line-height: 30px;
}
.code-wrap .code-title i {
      color: main-color;
      margin-right: 5px;
      vertical-align: middle;
}



