
.components-root {
  display: flex;
  flex-direction: row;
}
.components-root .components-nav {
    width: 120px;
    padding: 0 10px 10px 10px;
    text-align: center;
}
.components-root .components-nav .components-group {
      padding: 5px;
      margin-bottom: 10px;
}
.components-root .components-nav .components-title {
      background: #ededed;
      padding: 4px 0;
}
.components-root .components-nav a {
      display: block;
      padding-bottom: 10px;
      color: #999;
      font-size: 14px;
      margin-bottom: 5px;
}
.components-root .components-nav a.router-active {
        color: #e41b23;
}
.components-router-view {
  width: 0;
  flex: 1 1 auto;
  padding-right: 140px;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}



