
.components-view {
  margin: 10px;
}
.box {
  width: 400px;
  height: 120px;
  margin: auto;
}
.qc-slide_carousel {
  width: 100%;
}
.content {
  padding: 0;
  margin: 0;
  height: 40px;
  line-height: 40px;
}
.container {
  width: 900px;
  height: 150px;
  margin: auto;
  overflow: hidden;
}
.container .container-item {
    width: 300px;
    display: inline-block;
    height: 150px;
    text-align: center;
}
.container .container-item .container-content {
      line-height: 150px;
}
.indicator {
  margin-top: 20px;
  text-align: center;
}



